import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap'
import {deleteProductById} from "../service/ProductService";
import {toast} from "react-toastify";
import {Modal,Button} from "react-bootstrap";


const ModalDelete = ({handleReloadPage,showModal, idDelete, closeModal}) => {
    const deleteStudent = async () => {
        await deleteProductById(idDelete);
        handleReloadPage();
        closeModal();
        toast.success('Xo thnh cng!');
    }

    return (
        <Modal show={showModal} onHide={closeModal}> {/* onHide l du X  gc */}
            <Modal.Header closeButton>
                <Modal.Title>Xo Sn Phm</Modal.Title>
            </Modal.Header>
            <Modal.Body>Bn c mun xo sn phm c id: {idDelete}</Modal.Body>
            <Modal.Footer>
                <Button variant="outline-success" onClick={()=>(closeModal())}>
                    Hu
                </Button>
                <Button variant="outline-danger" onClick={deleteStudent}>
                    Xo
                </Button>
            </Modal.Footer>
        </Modal>
    );
}
export default ModalDelete;